@import url("https://fonts.googleapis.com/css2?family=Work+Sans:ital,wght@0,100..900;1,100..900&display=swap");
:root {
  --background: rgb(111, 111, 232);
  --tipo-principal: "Work Sans", sans-serif;
}

@media (prefers-reduced-motion: reduce) {
  * {
    animation: none !important;
    transition: none !important;
  }
}
* {
  margin: 0;
  padding: 0;
  min-width: 0;
  border: 0;
  box-sizing: border-box;
  vertical-align: baseline;
}

body {
  min-height: 100dvh;
  font-family: "Work Sans", sans-serif;
  background-color: white;
}
body.hidden {
  overflow: hidden;
}

#page {
  overflow: hidden;
}

h1,
h2,
h3 {
  text-wrap: balance;
}

p {
  text-wrap: pretty;
}

img,
picture,
video,
iframe,
figure {
  max-width: 100%;
  width: 100%;
  display: block;
  margin: 0;
  object-fit: cover;
  object-position: center center;
}

figure img {
  height: 100%;
}

a {
  display: block;
  text-decoration: none;
  color: inherit;
  font-size: inherit;
}

a:visited {
  color: inherit;
}

p a {
  display: inline;
}

ul {
  margin: 0;
}

li {
  list-style-type: none;
}

html {
  scroll-behavior: smooth;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
span,
a,
strong,
blockquote,
i,
b,
u,
em {
  font-size: 1em;
  font-weight: inherit;
  font-style: inherit;
  text-decoration: none;
  color: inherit;
}

strong {
  font-weight: bold;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

::selection {
  background-color: var(--background);
  color: white;
}

form,
input,
textarea,
select,
button,
label {
  font-family: inherit;
  font-size: inherit;
  background-color: transparent;
  color: inherit;
  display: block;
  appearance: none;
  outline: none;
}

table,
tr,
td {
  border-collapse: collapse;
  border-spacing: 0;
}

svg {
  width: 100%;
  display: block;
  fill: currentColor;
}

body {
  min-height: 100vh;
  font-size: 100%;
  font-family: var(--tipo-principal);
  line-height: 1.4em;
  font-smooth: always;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.max-width {
  box-sizing: border-box;
  max-width: 2000px;
  margin: 0 auto;
}
@media (max-width: 2300px) {
  .max-width {
    max-width: 1500px;
  }
}
@media (max-width: 1800px) {
  .max-width {
    max-width: calc(100vw - 240px);
  }
}
@media (max-width: 1440px) {
  .max-width {
    max-width: calc(100vw - 190px);
  }
}
@media (max-width: 992px) {
  .max-width {
    max-width: calc(100vw - 80px);
  }
}
@media (max-width: 480px) {
  .max-width {
    max-width: 100vw;
    padding: 0;
  }
}
@media (min-width: 320px) and (max-width: 480px) {
  .max-width {
    max-width: 100vw;
    padding: 0 20px;
  }
}

.p {
  font-size: clamp(14px, 2vw, 18px);
  font-weight: 400;
  color: white;
}
.p b {
  font-weight: 700;
}

.button {
  width: max-content;
  display: flex;
  padding: 10px 20px;
  color: white;
  transition: all 0.3s ease;
  font-size: clamp(14px, 2vw, 16px);
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.h1 {
  line-height: 28px;
  font-size: 32px;
  font-weight: 700;
}
@media screen and (min-width: 768px) {
  .h1 {
    font-size: 36px;
  }
}
@media screen and (min-width: 1024px) {
  .h1 {
    font-size: 40px;
  }
}
@media screen and (min-width: 1440px) {
  .h1 {
    font-size: 44px;
  }
}
@media screen and (min-width: 1800px) {
  .h1 {
    font-size: 48px;
  }
}

.h2 {
  font-size: 20px;
  line-height: normal;
  font-weight: 700;
  color: white;
  text-transform: uppercase;
  letter-spacing: 7.6px;
}
@media screen and (min-width: 768px) {
  .h2 {
    font-size: 34px;
  }
}
@media screen and (min-width: 1024px) {
  .h2 {
    font-size: 38px;
  }
}

.h3 {
  font-size: clamp(22px, 2.5vw, 28px);
}

.content p {
  display: block;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-size: 18px;
}
.content h1 {
  display: block;
  font-size: 2em;
  margin-block-start: 0.67em;
  margin-block-end: 0.67em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: bold;
  line-height: 2.13rem;
}
.content h2 {
  display: block;
  font-size: 1.5em;
  margin-block-start: 0.83em;
  margin-block-end: 0.83em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: bold;
}
.content h3 {
  display: block;
  font-size: 1.17em;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: bold;
}
.content h4 {
  display: block;
  margin-block-start: 1.33em;
  margin-block-end: 1.33em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: bold;
}
.content h5 {
  display: block;
  font-size: 0.83em;
  margin-block-start: 1.67em;
  margin-block-end: 1.67em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: bold;
}
.content h6 {
  display: block;
  font-size: 0.67em;
  margin-block-start: 2.33em;
  margin-block-end: 2.33em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: bold;
}
.content ol {
  display: block;
  list-style-type: decimal;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  padding-inline-start: 40px;
}
.content ul {
  display: block;
  list-style-type: disc;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  padding-inline-start: 40px;
}
.content li {
  list-style: initial;
}
.content b,
.content strong {
  font-weight: bold;
}

.header {
  top: 0;
  width: 100%;
  z-index: 80;
  background-color: rgba(0, 0, 0, 0.468);
}
.header__wrapper {
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__top {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header .custom-logo {
  height: 100%;
  max-height: 5rem;
  object-fit: contain;
}
.header__toggle {
  position: relative;
  width: 40px;
  transition: all 0.4s ease-in-out;
}
.header__toggle.active {
  position: absolute;
  top: 30px;
  right: 30px;
  z-index: 999;
}
@media screen and (min-width: 940px) {
  .header__toggle {
    display: none;
  }
}
.header__ul {
  display: flex;
  gap: 16px;
}
.header__menu-movil {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.565);
  opacity: 0;
  transition: all 0.4s ease;
}
.header__menu-movil img {
  object-fit: contain;
}
.header__menu-movil--content {
  position: absolute;
  top: 0;
  left: -100%;
  width: 50%;
  height: 100%;
  padding: 40px 20px;
  background-color: #00358E;
  transition: all 0.4s ease;
}
.header__menu-movil .header__menu {
  padding: 30px 0;
  color: white;
}
.header__menu-movil.active {
  z-index: 990;
  opacity: 1;
}
.header__menu-movil.active .header__menu-movil--content {
  left: 0;
}
.header__p {
  color: #fff;
  text-align: center;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
.header__navigation {
  width: 100%;
}
.header__menu {
  font-weight: 600;
}
@media screen and (min-width: 1024px) {
  .header__menu {
    width: 100%;
    padding: 16px 0;
    display: flex;
    justify-content: space-around;
    color: white;
  }
}
.burger {
  position: relative;
  width: 40px;
  height: 30px;
  background: transparent;
  cursor: pointer;
  display: block;
}

.burger input {
  display: none;
}

.burger span {
  display: block;
  position: absolute;
  height: 4px;
  width: 100%;
  background: white;
  border-radius: 9px;
  opacity: 1;
  left: 0;
  transform: rotate(0deg);
  transition: 0.25s ease-in-out;
}

.burger span:nth-of-type(1) {
  top: 0px;
  transform-origin: left center;
}

.burger span:nth-of-type(2) {
  top: 50%;
  transform: translateY(-50%);
  transform-origin: left center;
}

.burger span:nth-of-type(3) {
  top: 100%;
  transform-origin: left center;
  transform: translateY(-100%);
}

.burger input:checked ~ span:nth-of-type(1) {
  transform: rotate(45deg);
  top: 0px;
  left: 5px;
}

.burger input:checked ~ span:nth-of-type(2) {
  width: 0%;
  opacity: 0;
}

.burger input:checked ~ span:nth-of-type(3) {
  transform: rotate(-45deg);
  top: 28px;
  left: 5px;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes shake {
  0% {
    rotate: 0;
  }
  25% {
    rotate: 7deg;
  }
  50% {
    rotate: -7deg;
  }
  75% {
    rotate: 1deg;
  }
  100% {
    rotate: 0;
  }
}